* {
   margin: 0;
   padding: 0;
}

.searchInput {
   width: 100%;
   height: 100%;
   outline: none;
   border: none;
}

.searchTitle {
   width: 100%;
   height: 12px;
   font-size: 14px;
   font-weight: bold;
   font-style: normal;
   font-stretch: normal;
   line-height: 0.86;
   letter-spacing: normal;
   color: #808080;
}

.searchContainer {
   margin-top: 4px;
   width: 100%;
   height: 76%;
   border: solid 2px #000000;
   background-color: #ffffff;
   display: flex;
   align-items: center;
}

.keyWord {
   width: 111px;
   height: 32px;
   border-radius: 4px;
   border: solid 1px #dfdedf;
   background-color: #ffffff;
   margin-left: 11px;
   display: flex;
   align-items: center;
   justify-content: space-between;
}

.keyWordValue {
   margin-left: 16px;
   font-size: 16px;
   font-weight: normal;
   font-style: normal;
   font-stretch: normal;
   line-height: normal;
   letter-spacing: normal;
   color: #000000;
}

.searchInputArea {
   width: 80%;
   height: 100%;
   outline: none;
   border: none;
   padding-left: 16px;
}

.closeImg {
   width: 24px;
   height: 24px;
   object-fit: contain;
   margin-right: 8px;
   cursor: pointer;
}



::placeholder {
   width: 100%;
   height: 20px;
   font-size: 16px;
   font-weight: bold;
   font-style: normal;
   font-stretch: normal;
   line-height: normal;
   letter-spacing: normal;
   color: #cacaca;
}